      </svg>
      <div class="container">
        <div class="checkout-addr">
          <div class="page-title-normal">
            <h2 class="page-title-h2">
              <span>check out</span>
            </h2>
          </div>
          <!-- process step -->
          <div class="check-step">
            <ul>
              <li class="cur">
                <span>Confirm</span> address</li>
              <li>
                <span>View your</span> order</li>
              <li>
                <span>Make</span> payment</li>
              <li>
                <span>Order</span> confirmation</li>
            </ul>
          </div>

          <!-- address list -->
          <div class="page-title-normal checkout-title">
            <h2>
              <span>Shipping address</span>
            </h2>
          </div>
          <div class="addr-list-wrap">
            <div class="addr-list">
              <ul>
                <li v-for='(item,index) in addressFilter' v-if='addressList && addressList.length > 0'
                  :class="{check: checkIndex === index}" @click='selectAddress(item,index)'>
                  <dl>
                    <dt>{{item.userName}}</dt>
                    <dd class="address">{{item.street}}</dd>
                    <dd class="tel">{{item.tel}}</dd>
                  </dl>
                  <div class="addr-opration addr-del" @click='delAddress(item.street)'>
                    <a href="javascript:;" class="addr-del-btn">
                      <svg class="icon icon-del">
                        <use xlink:href="#icon-del"></use>
                      </svg>
                    </a>
                  </div>
                  <div class="addr-opration addr-set-default" v-if='!item.default' @click='setDefault(item.street)'>
                    <a href="javascript:;" class="addr-set-default-btn">
                      <i>Set default</i>
                    </a>
                  </div>
                  <div class="addr-opration addr-default" v-if='item.default'>Default address</div>
                </li>
                <li class="addr-new">
                  <div class="add-new-inner">
                    <i class="icon-add">
                      <svg class="icon icon-add">
                        <use xlink:href="#icon-add"></use>
                      </svg>
                    </i>
                    <p>Add new address</p>
                  </div>
                </li>
              </ul>
            </div>

            <div class="shipping-addr-more">
              <a class="addr-more-btn up-down-btn" href="javascript:;" @click='expand' 
              :class="{open:limit > 3}">
                more
                <i class="i-up-down">
                  <i class="i-up-down-l"></i>
                  <i class="i-up-down-r"></i>
                </i>
              </a>
            </div>
          </div>

          <!-- shipping method-->
          <div class="page-title-normal checkout-title">
            <h2>
              <span>Shipping method</span>
            </h2>
          </div>
          <div class="shipping-method-wrap">
            <div class="shipping-method">
              <ul>
                <li class="check">
                  <div class="name">Standard shipping</div>
                  <div class="price">Free</div>
                  <div class="shipping-tips">
                    <p>Once shipped，Order should arrive in the destination in 1-7 business days</p>
                  </div>
                </li>
              </ul>
            </div>
          </div>
          <div class="next-btn-wrap">
            <router-link :to="{name: 'OrderConfirm',query:{addressId:selectedAddId}}" class="btn btn--m btn--red" href="#">Next</router-link>
          </div>
        </div>
      </div>
    </div>
    <sxFooter></sxFooter>
  </div>
</template>
<style>

</style>
<script>
import sxHeader from '../components/sxHeader'
import sxFooter from '../components/sxFooter'
import bread from '../components/bread'
export default {
  data() {
    return {
      addressList: [],
      limit:3,
      checkIndex:0,
      selectedAddId: ''
    }
  },
  computed: {
    addressFilter(){
      return this.addressList.slice(0,this.limit)
    },
  },
  methods: {
    selectAddress(item,index) {
      if(item){
         this.checkIndex = index;
         this.selectedAddId =item.street
      } else {
        this.addressList.forEach(item => {
          if(item.default === true) {
            this.selectedAddId = item.street;
            this.checkIndex = this.addressList.indexOf(item)
          } else {
             this.selectedAddId = this.addressList[0].street
          }
        })
      }
    },
    getAddressList() {
      this.axios.get('/api/users/addressList').then(res => {
        res && (res = res.data)
        if (res.code === 0) {
          this.addressList = res.data
          this.selectAddress()
        }
      })
    },
    expand(){
      switch(this.limit){
        case 3: 
        this.limit = this.addressList.length;
        break;
        default:
        this.limit = 3
      }
    },
    setDefault(id){
       this.axios.post('/api/users/setDefault',{addressId:id}).then(res => {
        res && (res = res.data)
        if (res.code === 0) {
          this.getAddressList()
        }
      })
    },
    delAddress(id){
       this.axios.post('/api/users/delAddress',{addressId:id}).then(res => {
        res && (res = res.data)
        if (res.code === 0) {
          this.getAddressList()
        }
      })
    }

  },
  created() {
    this.getAddressList()
    
  },
  components: {
    sxFooter,
    sxHeader,
    bread
  }
}
</script>



